.partners__title {
  margin-top: 100px;
}

.slider__container_partners {
  width: 80%;
  padding-bottom: 30px;
}

.partners-slider__navigation-btn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  border: none;
  background: none;
}

.partners-slider__navigation-btn_left {
  left: 15px;
}

.partners-slider__navigation-btn_right {
  right: 15px;
}

.partners-slider__navigation-btn_right > img {
  transform: rotateZ(180deg);
}

.slider__container_partners .swiper-pagination {
  bottom: 0;
}